.mainSection {
	background: #385e94;
}
/* Force scrollbar in order to make all pages the same */

body {
	font-family: -apple-system, BlinkMacSystemFont, "avenir next", "avenir",
		"helvetica neue", "helvetica", "ubuntu", "roboto", "noto", "segoe ui",
		"arial", sans-serif;
	background: #385e94;
	color: white;
	overflow-x: hidden;
}

/* Small mobile sizes*/

@media (max-width: 375px) {
	body {
		max-width: 340px;
		padding-left: 5.5%;
	}
}

@media (max-width: 375px) {
	#articleListMainGrid {
		margin-left: -5.5%;
		margin-right: -5.5%;
		justify-items: center;
	}
}

@media only screen and (max-width: 375px) {
	#listRow {
		margin-left: 0%;
		display: flex;
		justify-content: flex-start;
	}
}
@media only screen and (max-width: 375px) {
	div.articleContent > div > img {
		max-width: 315px;
	}
}

/* Large mobile sizes*/

@media (max-width: 425px) {
	body {
		max-width: 380px;
		padding-left: 10%;
	}
}

@media (max-width: 425px) {
	#articleListMainGrid {
		margin-left: -5.5%;
		margin-right: -5.5%;
		justify-items: center;
	}
}

@media only screen and (max-width: 425px) {
	#listRow {
		margin-left: 0%;
		display: flex;
		justify-content: flex-start;
	}
}

@media only screen and (max-width: 425px) {
	div.articleContent > div > img {
		max-width: 300px;
		margin-right: -5.5%;
	}
}

