/* Laptop+ Sizes */

@media only screen and (min-width: 768px) {
	div.articleContent > div > img {
		max-width: 100%;
	}
}

@media only screen and (min-width: 768px) {
	.leftHeaderColText {
		margin-left: 55%;
		margin-top: 0%;
	}
}

a {
	display: inline;
	font-weight: bold;
	border-bottom: 3px solid #7e9fce;
	padding-bottom: 1px;
	color: white;
}

a:hover {
	border-bottom: 3px solid #fee18b;
	color: white;
}

i {
	color: #7e9fce;
}
i:hover {
	color: white;
}

.underlineHeaderText {
	display: inline;
	border-bottom: 3px solid #7e9fce;
	padding-bottom: 1px;
}

.underlineHeaderText:hover {
	border-bottom: 3px solid #fee18b;
}

#dateText {
	font-size: smaller;
	color: #7e9fce;
	padding-top: 1%;
}

#leadText {
	font-size: medium;
	margin-top: -1%;
}

ul {
	padding: 0%;
}

#headerRow {
	height: 100px;
}

.rightHeaderCol {
	margin-top: 3.4%;
}

.articleContent {
	font-size: large;
	line-height: 2.5rem;
	padding-bottom: 5%;
}

.contentTitle {
	padding-bottom: 1%;
	font-size: 3rem;
	font-weight: bolder;
}

.contentDate {
	padding-bottom: 1%;
	color: #7e9fce;
	font-weight: bold;
	font-size: medium;
}

#articleListGrid {
	padding-top: 3%;
}

#mainGrid {
	align-self: center;
	padding-top: 3%;
}

/* Force scrollbar on root page on devices larger than tablets */
@media only screen and (min-width: 768px) {
	.mainSection {
		overflow-x: hidden;
		overflow-y: scroll;
	}
}

@media only screen and (min-width: 768px) {
	#centerCol {
		padding-left: 10%;
		padding-right: 10%;
	}
}

@media only screen and (min-width: 768px) {
	#listRow {
		margin-left: 6.5%;
		display: flex;
		justify-content: flex-start;
	}
}

#aboutText {
	font-size: large;
	font-weight: 100;
}

#aboutImg {
	margin-top: 6%;
}

#articleList {
	margin-bottom: 2%;
}

#articleListMainGrid {
	padding-top: 2%;
	justify-items: center;
}

#projectContent {
	padding-top: 5%;
}

.projectPadding {
	margin-top: 4%;
}

.projectPaddingBottom {
	padding-bottom: 20%;
}

#toTopButton {
	font-size: medium;
	float: right;
}

#formField {
	color: white;
	font-size: medium;
}

#formButton {
	border: 1px solid white;
	background: #385e94;
	color: white;
}

#formButton:hover {
	border: 1px solid #fee18b;
}

#subscribeHeader {
	padding-bottom: 2%;
	padding-top: 3%;
}

#newsletterHeader {
	padding-top: 3%;
}

#MERGE0 {
	background: #385e94;
	border: 1px solid white;
	color: white;
}
#MERGE0:focus {
	background: white;
	color: black;
	border: 1px solid #fee18b;
}

#MERGE0:hover {
	border: 1px solid #fee18b;
}

#MERGE1 {
	background: #385e94;
	border: 1px solid white;
	color: white;
}

#MERGE1:focus {
	background: white;
	color: black;
	border: 1px solid #fee18b;
}

#MERGE1:hover {
	border: 1px solid #fee18b;
}

.aTagStyleBlocker {
	border: none;
	padding-bottom: none;
	color: white;
}

.aTagStyleBlocker:hover {
	color: white;
	border: none;
}

#hiddenDirTag {
	display: none;
}

#disqusEmbed {
	margin-top: 5%;
	margin-bottom: 5%;
}

#socialEmbed {
	margin-top: 5%;
	margin-bottom: 5%;
}

#newsletterEmbed {
	margin-bottom: 10%;
}

.projectSegments {
	background: #385e94 !important;
	border: 2px solid #7e9fce !important;
	margin-bottom: 10% !important;
}
.activeLink {
	border-bottom: 3px solid #fee18b !important;
}

.underlineLinkText {
	font-size: 1.3rem;
	display: inline;
	border-bottom: 3px solid #7e9fce;
	padding-bottom: 1px;
}

.underlineLinkText:hover {
	border-bottom: 3px solid #fee18b;
}

#socialList {
	display: flex;
	float: left;
}

#socialDivider {
	margin-top: 7%;
}

#socialGrid {
	margin-top: -10%;
}

#withImageArticleImage {
	margin-top: 6%;
}

div.articleContent > div > img {
	padding-top: 2%;
	padding-bottom: 2%;
}

#twitterPromptDiv {
	padding-bottom: 0;
	margin: 0;
}

.articleDetailContent {
	padding-top: 5%;
}

#bookshelfContent {
	padding-top: 4%;
}

.yellow_text {
	color: #fee18b !important;
  font-weight: bold;
  text-decoration-color: #fee18b !important;
}

.lightblue_text {
	color: #7e9fce !important;
  font-weight: bold;
  text-decoration-color: #7e9fce !important;
}

.book_a_tag {
	text-decoration: underline !important;
  color: white;
  font-size: medium;
}

.book_a_tag:hover {
  border-bottom: 0px !important;
  font-size: medium;
}

#book_title_div {
	margin-top: 2% !important;
	margin-bottom: 3% !important;
}
